"use strict";

require("../Styles/reInsiderverzeichnisLookupSelect2.css");
require("select2_css");

(function(pNameSpace) {
    pNameSpace.LookupSelect2 = new function() {
        var self = this;
        self.$ = require("jquery");
        //var $ = self.$; //window.$ = window.jQuery = require('jquery');
        require("select2");//(self.$);

        self.selectToSelect2Fields = new Array();
        self.lookupFieldConfig = require("../reInsiderverzeichnisLookupSelect2Config.json");

        self.selectField = function (pSelectId, pFormContext) {
            var field = this;
            field.id = pSelectId;
            field.context = pFormContext;
            field.fieldSchema = pFormContext.fieldSchema;
            field.data =  function() {
                var data = new Array();
                self.$.each(field.fieldSchema.Choices, function (index, item) {
                    data.push({ id: item.LookupId, value: item.LookupValue, text: item.LookupValue});
                });
                return data;
            }
        };

        self.selectTemplate =  function (state) {
            if (!state.id) { return state.text; }
            var result = state.text;
            if(state.fieldName && self.lookupFieldConfig[state.fieldName].template) {
                result = self.lookupFieldConfig[state.fieldName].template;
                self.$.each(self.lookupFieldConfig[state.fieldName].displayField, function(index, field) {
                    result = result.replace(new RegExp("\\{" + index + "\\}","g"), (state.item[field] ? state.item[field] : ""));
                });
                result = self.$(result);
            }
            return result;
        }

        self.newEditFormTemplate = function (renderCtx) {
            var formCtx = SPClientTemplates.Utility.GetFormContextForCurrentField(renderCtx);
            var id = formCtx.fieldName + "__" + formCtx.fieldSchema.Id + "__" + formCtx.fieldSchema.FieldType;

            formCtx.registerGetValueCallback(formCtx.fieldName, function() {
                var value = self.$("#" + id).select2("data");
                var valueArray = [];
                if (value) {
                    if (value.length) {
                        for (var i = 0; i < value.length; i++) {
                            valueArray.push(value[i].id);
                            valueArray.push(value[i].value);
                        }
                    } else {
                        valueArray.push(value.id);
                        valueArray.push(value.value);
                    }
                }
                return valueArray.join(";#");
            });

            self.selectToSelect2Fields.push(new self.selectField(id, formCtx));
            var selectElement = document.createElement("select");
            selectElement.setAttribute("id", id);
            selectElement.setAttribute("title", formCtx.fieldName);

            if(!formCtx.fieldSchema.Required) {
                var option = document.createElement("option");
                selectElement.appendChild(option);  
            }

            return selectElement.outerHTML;
        };

        self.onPostRender = function (renderCtx) {
            self.$.each(self.selectToSelect2Fields, function(index, field) {
                //setting current value
                if(field.context.fieldValue) {
                    var values = field.context.fieldValue.split(";#");
                    var data = new Array();
                    if (field.fieldSchema.AllowMultipleValues) {
                        for (var i = 0; i + 1 < values.length; i++) {
                            data.push({ id: values[i], text: values[++i] });
                        }
                    } else if (values.length === 2) {
                        data.push({ id: values[0], text: values[1] });
                    }
                    if (data.length > 0 || values.length === 2) self.$('select[id="' + field.id + '"]').append("<option value='"+values[0]+"' selected>"+values[1]+"</option>");
                }

                if(self.lookupFieldConfig[field.fieldSchema.Name].query) {                     
                    self.$('select[id="' + field.id + '"]').select2({
                        ajax: {
                            url: _spPageContextInfo.webAbsoluteUrl + "/_api/web/lists/getbytitle('" + self.lookupFieldConfig[field.fieldSchema.Name].list + "')/Items",
                            type: "GET",
                            headers: {"accept": "application/json;odata=verbose"},
                            data: function(term, page) {
                                var query = new Array();
                                if(self.lookupFieldConfig[field.fieldSchema.Name].query.select)
                                    query.push("$select=" + self.lookupFieldConfig[field.fieldSchema.Name].query.select);
                                if(self.lookupFieldConfig[field.fieldSchema.Name].query.orderby)
                                    query.push("$orderby=" + self.lookupFieldConfig[field.fieldSchema.Name].query.orderby);
                                if(self.lookupFieldConfig[field.fieldSchema.Name].query.filter) {
                                    var filter = "$filter=" + self.lookupFieldConfig[field.fieldSchema.Name].query.filter
                                    if(term.term && self.lookupFieldConfig[field.fieldSchema.Name].searchable) {
                                        if(self.lookupFieldConfig[field.fieldSchema.Name].searchableFields) {
                                            filter += "%20and%20(";
                                            self.$.each(self.lookupFieldConfig[field.fieldSchema.Name].searchableFields, function (index, item) {
                                                if(index !== 0) filter += "%20or%20";
                                                filter += "substringof('" + term.term + "'," + item + ")";
                                            });
                                            filter += ")";
                                        } else {
                                            filter += ("%20and%20(substringof('" + term.term + "'," + self.lookupFieldConfig[field.fieldSchema.Name].displayField + ") ");
                                        }    
                                    }
                                    query.push(filter);
                                }

                                /*query.push("$top=" + field.fieldSchema.MaxChoicesSyncCount);
                                if (page > 1) {
                                    query.push("$skip=" + ((page - 1) * field.fieldSchema.MaxChoicesSyncCount));
                                }*/

                                return query.join("&");

                            },
                            processResults: function(data, params) {
                                var results = new Array();
                                self.$.each(data.d.results, function(index, item) {
                                    var text = "";
                                    if (typeof item[self.lookupFieldConfig[field.fieldSchema.Name].displayField] === 'string') {
                                        text = item[self.lookupFieldConfig[field.fieldSchema.Name].displayField];
                                    } else {
                                        //ToDo mutliple display fields
                                    }
                                    results.push({ id: item.ID, value: item[self.lookupFieldConfig[field.fieldSchema.Name].lookup], text: text, item: item, fieldName: field.fieldSchema.Name });
                                });
                                return { results: results };
                            }
                        },
                        delay: 250,
                        minimumResultsForSearch: self.lookupFieldConfig[field.fieldSchema.Name].searchable ? 1 : Infinity,
                        allowClear: !field.fieldSchema.Required,
                        placeholder: self.lookupFieldConfig[field.fieldSchema.Name].placeholder ? self.lookupFieldConfig[field.fieldSchema.Name].placeholder : (self.lookupFieldConfig[field.fieldSchema.Name].searchable ? "Type or Select" : "Select"),
                        multiple: field.fieldSchema.AllowMultipleValues,
                        width: self.lookupFieldConfig[field.fieldSchema.Name].width,
                        templateResult: self.selectTemplate,
                        templateSelection: self.selectTemplate
                    });
                } else {
                    self.$('select[id="' + field.id + '"]').select2({
                        data: field.data(),
                        minimumResultsForSearch: self.lookupFieldConfig[field.fieldSchema.Name].searchable ? 1 : Infinity,
                        allowClear: !field.fieldSchema.Required,
                        placeholder: self.lookupFieldConfig[field.fieldSchema.Name].placeholder ? self.lookupFieldConfig[field.fieldSchema.Name].placeholder : (self.lookupFieldConfig[field.fieldSchema.Name].searchable ? "Type or Select" : "Select"),
                        multiple: field.fieldSchema.AllowMultipleValues,
                        width: self.lookupFieldConfig[field.fieldSchema.Name].width
                    });
                }
                
            });

            self.selectToSelect2Fields = new Array();
        };

        self.registerLookupSelect2Context = function () {

            var lookupSelect2Context = {};
            lookupSelect2Context.Templates = {};
            lookupSelect2Context.Templates.Fields = {
                "Lookup": {
                    "NewForm": self.newEditFormTemplate,
                    "EditForm": self.newEditFormTemplate
                }
            };
            lookupSelect2Context.OnPostRender = self.onPostRender;

            SPClientTemplates.TemplateManager.RegisterTemplateOverrides(lookupSelect2Context);
        };

        self.init = function() {

            self.registerLookupSelect2Context();
        };

        self.init();
    };
})
((window.HannRe = window.HannRe || {}) &&
(window.HannRe.Insiderverzeichnis = window.HannRe.Insiderverzeichnis || {}));


